/**
 *  : margeValueToArr(arr, key, addKey)
 *     ,    pirce       .
 * @param arr type 'Array'
 * @param key type 'String' :      key
 * @param addKey type 'String' :      key
 * return type Array
 */
const margeValueToArr = (arr, key, addKey) => {
  if (!Array.isArray(arr)) {
    throw new TypeError('   . Array');
  }
  let list = JSON.parse(JSON.stringify(arr));
  return list.reduce((a, b) => {
    if(!a.slice(-1)[0]){
      a.push(b);
    }else{
      if(a.slice(-1)[0][key] === b[key]){
        a.slice(-1)[0][addKey] += b[addKey];
      }else{
        a.push(b);
      }
    }
    return a;
  }, []);
};

export default margeValueToArr;



