
/**
 *  : insertSortingArr
 *       . (           )
 * @param arr :     (type : Array)
 * @param item :     (type : Object)
 * @param key : '   '
 * return type Array
 */
const insertSortingArr = (arr, item, key) => {
  if (!Array.isArray(arr) || !item) {
    throw new TypeError('   ,  ');
  }
  //          , deepcopy .
  let newArr = JSON.parse(JSON.stringify(arr));
  if (newArr.length === 0) {
    newArr.push(item);
  } else {
    if (newArr[newArr.length - 1][key] < item[key]) {
      newArr.push(item);
    } else {
      for (let i = 0; i < newArr.length; i++) {
        if (newArr[i][key] > item[key]) {
          newArr.splice(i, 0, item);
          break;
        }
      }
    }
  }
  return newArr;
};

export default insertSortingArr;


